
























































































































.bx--tile--expandable {
  padding: 0rem;
  &.bx--tile--is-expanded {
    cursor: inherit;
    &:hover {
      background: white;
    }
    &:focus {
      outline: none;
    }
  }
}

.bx--tile-expandable-atf {
  padding: 1rem 1rem 0;
  position: relative;
}
.bx--tile-content__below-the-fold {
  padding: 0rem 1rem 1rem;
}

.bx--tile__chevron {
  padding: 0.7rem;
  height: 2.4rem;
  min-height: 2.4rem;
  bottom: unset;

  svg {
    margin-left: 0;
  }
}
