















































































































































































.record {
}
.field-line {
  position: relative;
  &::before {
    content: '';
    position: absolute;
    background-color: #888;
    width: 2px;
    height: calc(100% - 0.7rem);
    left: -0.5rem;
  }
}

.field-line--connector {
  position: relative;
  &::before {
    content: '';
    position: absolute;
    width: 1rem;
    height: 2px;
    left: -0.5rem;
    top: 0.7rem;
    background-color: #888;
  }
}

.pipe-between:not(:last-child) {
  &::after {
    content: ' | ';
  }
}
